import { each } from 'lodash';
import { gsap, SplitText } from 'vendors/gsap';

export default class SylvestreAnimation implements AnimationInterface {
  el?:HTMLElement;
  split?:SplitText;
  tl?:Timeline;

  constructor(els:{
    el?:HTMLElement,
  }) {
    this.el = els.el;

    this.init();

    return this;
  }

  init() {
    if (this.el) {

      this.split = new SplitText(this.el, {
        type: 'lines,words,chars',
        charsClass: 'char',
        linesClass: 'line',
        wordsClass: 'word',
        reduceWhiteSpace: false,
      });
      
      this.tl = gsap.timeline(
        { 
          paused: true,
          autoRemoveChildren: false,
          repeatRefresh: true,
        }
      );

      if (this.tl && this.split) {
        each(this.split.lines, (word:any, index:number) => {
          const words = word.querySelectorAll('.char');
          this.tl?.add(
            gsap.fromTo(words, 
              { rotation:'45deg', yPercent:110 }, 
              { rotation:0, duration: 1.2, ease: "elastic.out(0.55, 0.5)", stagger: 0.025, yPercent: 0 }
            ), 
            index * 0.2
          )
        })
      }

    }
  }

  enter = () => {
    if (this.tl) {
      this.tl.timeScale(1).play(0);
    }
  }

  display = () => {
    if (this.tl) {
      this.tl.totalProgress(1, false);
    }
  }

  update = () => {
    if (this.el) {
      this.split = new SplitText(this.el, {
        type: 'lines,words,chars',
        charsClass: 'char',
        linesClass: 'line',
        wordsClass: 'word',
        reduceWhiteSpace: false,
      });
    }
  }
  
  leave = () => {
    if (this.tl) {
      this.tl.timeScale(2).reverse();
    }
  }

  dispose() {
    this.split?.revert();
    this.tl?.kill();
    this.tl = undefined;
    this.split = undefined;
  }
}
